  {"id":"50","lng":"Google Maps","title":"Draws Map and overlays","dsc":"\r\n","code":"Creates map\r\n---------\r\n HTML\r\n      <script src=\"https:    maps.googleapis.com  maps  api  js?key={key}\">\r\n      <style>\r\n        #map {\r\n                height: 100%;\r\n              }\r\n      <  style>\r\n      <div id=\"map\"><  div>\r\n      <script>\r\n      var map = new google.maps.Map(document.getElementById(\"map\"), {\r\n                center: {\r\n                  lat: pos.lat,\r\n                  lng: pos.lng\r\n                },\r\n                zoom: 8\r\n              })\r\n      <  script>\r\n\r\nAdds EventListener on map\r\n-------------------------\r\nmap.addListener('click', function(e){\r\n    gets geolocation\r\nconsole.log(e.latLng.toString())\r\n})\r\n\r\nCreates POINT (lat  lng)\r\n--------------------\r\nvar pos = {lat: number ,lng: number}\r\n\r\nvar point = new google.maps.LatLng( pos.lat , pos.lng )\r\n\r\n\r\n\r\nCreates Marker\r\n-------------\r\nvar markers = []\r\n\r\n    adss marker\r\nvar marker = new google.maps.Marker({\r\n              map: map,\r\n          position: point,\r\n              label:'Label'\r\n        });\r\nmarkers.push(marker)\r\n\r\nmarkers.setMap(map)\r\n\r\n     Adds single marker\r\nmarkers[0].setMap(map)\r\n\r\n     Removes single marker\r\nmarkers[0].setMap(null)\r\n\r\n\r\n\r\nCreates Polyline\r\n-----------------\r\n\r\nvar poliPoints = [\r\n                  {lat: number,lng: number},\r\n                ]\r\n\r\nvar poliPath = new google.maps.Polyline({\r\n    path: poliPoints,\r\n    strokeColor: \"#FF0000\",\r\n    strokeOpacity: 1.0,\r\n    strokeWeight: 2\r\n  });\r\n\r\n  poliPath.setMap(map);\r\n\r\n    Adss new polyLine point on existing path\r\npoliPath.getPath().setAt(poliPoints.length - 1, new google.maps.LatLng( number,number) );\r\n\r\n    Removes polyLine point on existing path\r\npoliPath.getPath().removeAt(poliPoints.length - 1, new google.maps.LatLng( number,number) );\r\n\r\n\r\nDraws Circle\r\n------------\r\nvar cir = new google.maps.Circle({\r\n    strokeColor: \"#FF0000\",\r\n    strokeOpacity: 0.8,\r\n    strokeWeight: 2,\r\n    fillColor: \"#FF0000\",\r\n    fillOpacity: 0.35,\r\n        map:map,\r\n    center: {lat:number,lng:number} ,\r\n    radius: 10000\r\n  })\r\n\r\n  cir.setMap (map)","editedCode":null
  }
